import React from 'react';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useDispatch } from 'react-redux';
import Model from '../../../components/UI/Model/Model';
import { chefApproveRejectSaga } from '../../../store/actions';
import { REJECT_REASON } from '../../../constants/errorConstants';

// eslint-disable-next-line no-unused-vars
const RejectChefModal = props => {
  const { chefId } = props;
  const closeModel = () => {
    const { setModelOpen } = props;
    setModelOpen(false);
  };

  const dispatch = useDispatch();

  const validationSchema = Yup.object({
    rejectReason: Yup.string().trim().required(REJECT_REASON),
  });

  const submitBtnHandler = values => {
    dispatch(
      chefApproveRejectSaga({
        id: chefId,
        isApproved: false,
        isRejected: true,
        ...values,
        closeModel,
      }),
    );
  };

  return (
    <Formik
      initialValues={{
        rejectReason: '',
      }}
      validationSchema={validationSchema}
      onSubmit={(values, { setSubmitting }) => {
        submitBtnHandler(values);
        setSubmitting(false);
      }}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        // isSubmitting,
      }) => (
        <Model
          closeModel={closeModel}
          modalId="modal-delete"
          headerTitle="Are you sure you want to Reject?"
        >
          <form onSubmit={handleSubmit}>
            <div className="from-group text-left">
              <textarea
                className="form-control"
                name="rejectReason"
                placeholder="Enter Reason Here"
                value={values.rejectReason}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <div className="error-message">
                {errors.rejectReason &&
                  touched.rejectReason &&
                  errors.rejectReason}
              </div>
            </div>
            <div className="clearfix m-t-md">
              <button
                type="button"
                className="btn btn-default m-r-sm"
                data-dismiss="modal"
                onClick={closeModel}
              >
                No
              </button>
              <button
                type="submit"
                className="btn btn-primary"
                data-toggle="modal"
                data-dismiss="modal"
                data-target="#modal-successb"
              >
                Yes
              </button>
            </div>
          </form>
        </Model>
      )}
    </Formik>
  );
};

export default RejectChefModal;
