import React, { Component } from 'react';
import GoogleMapReact from 'google-map-react';
import Marker from './Marker';

// eslint-disable-next-line no-unused-vars
class Map extends Component {
  constructor(props) {
    super(props);
    const reciveCoordinates =
      props.coordinates &&
      props.coordinates.latitude &&
      props.coordinates.longitude;
    this.state = {
      center: reciveCoordinates
        ? [
            Number(props.coordinates.latitude),
            Number(props.coordinates.longitude),
          ]
        : [28.7041, 77.1025],
      zoom: 15,
      draggable: true,
      lat: (reciveCoordinates && props.coordinates.latitude) || 28.7041,
      lng: (reciveCoordinates && props.coordinates.longitude) || 77.1025,
    };
  }

  // eslint-disable-next-line no-unused-vars
  shouldComponentUpdate(nextProps, nextState) {
    if (!nextProps.isReloadMap) {
      return true;
    }
    if (
      // this.props.draggable ||
      nextProps.coordinates.latitude !== this.state.lat &&
      nextProps.coordinates.longitude !== this.state.lng &&
      nextProps.isReloadMap
    ) {
      const reciveCoordinates =
        nextProps.coordinates &&
        nextProps.coordinates.latitude &&
        nextProps.coordinates.longitude;
      this.setState({
        center: reciveCoordinates
          ? [
              Number(nextProps.coordinates.latitude),
              Number(nextProps.coordinates.longitude),
            ]
          : [28.7041, 77.1025],
        lat: (reciveCoordinates && nextProps.coordinates.latitude) || 28.7041,
        lng: (reciveCoordinates && nextProps.coordinates.longitude) || 77.1025,
      });
      this.props.setIsReloadMap(false);
      return true;
    }
    return false;
  }

  // componentDidMount() {
  //   const { coordinates } = this.props;
  //   if (coordinates && coordinates.latitude && coordinates.longitude) {
  //     this.setState({
  //       center: [coordinates.latitude, coordinates.longitude],
  //       lat: coordinates.latitude,
  //       lng: coordinates.longitude,
  //     });
  //   }
  // }

  // eslint-disable-next-line no-unused-vars
  onCircleInteraction = (childKey, childProps, mouse) => {
    this.setState({
      draggable: false,
      lat: mouse.lat,
      lng: mouse.lng,
    });

    // eslint-disable-next-line no-unused-expressions
    this.props.getLatLng &&
      this.props.getLatLng({ latitude: mouse.lat, longitude: mouse.lng });
  };

  // eslint-disable-next-line no-unused-vars
  onCircleInteraction3 = (childKey, childProps, mouse) => {
    this.setState({ draggable: true });
  };

  onChange = ({ center, zoom }) => {
    this.setState({
      center,
      zoom,
    });
  };

  render() {
    const { isEditable } = this.props;
    return (
      <div style={{ height: '400px', width: '100%' }}>
        {isEditable ? (
          <GoogleMapReact
            // useTouchEvents
            bootstrapURLKeys={{
              key: 'AIzaSyD6M4Qgp4QzRf1PMYCsOTpDuVYSm0GAFbU',
            }}
            // gestureHandling="greedy"
            options={{
              gestureHandling: 'greedy',
            }}
            draggable={this.state.draggable}
            onChange={this.onChange}
            center={this.state.center}
            zoom={this.state.zoom}
            onChildMouseDown={this.onCircleInteraction}
            onChildMouseUp={this.onCircleInteraction3}
            onChildMouseMove={this.onCircleInteraction}
          >
            <Marker
              lat={Number(this.state.lat)}
              lng={Number(this.state.lng)}
              name={this.props.name}
              color="red"
            />
          </GoogleMapReact>
        ) : (
          <GoogleMapReact
            bootstrapURLKeys={{
              key: 'AIzaSyD6M4Qgp4QzRf1PMYCsOTpDuVYSm0GAFbU',
            }}
            center={{
              lat: Number(this.props.lat),
              lng: Number(this.props.lng),
            }}
            defaultZoom={15}
          >
            <Marker
              lat={Number(this.props.lat)}
              lng={Number(this.props.lng)}
              name={this.props.name}
              color="red"
            />
          </GoogleMapReact>
        )}
      </div>
    );
  }
}

export default Map;
