/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http';
import { Table } from '../../../components';
import { getAllDishAdminSaga } from '../../../store/actions';

// eslint-disable-next-line no-unused-vars
const MenuItems = props => {
  const { chefId } = props;
  const [mounted, setMounted] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [searchFlag, setSearchFlag] = useState(false);
  const [tableFilter, setTableFilter] = useState({});
  const setTableFilters = (columnName, orderBy, search, skip, limit) => {
    tableFilter.columnName = columnName;
    tableFilter.orderBy = orderBy;
    tableFilter.search = search;
    tableFilter.skip = skip;
    tableFilter.limit = limit;
    setTableFilter(tableFilter);
  };

  const dispatch = useDispatch();

  const {
    dishes: { list: allData, totalRecords },
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.dish);

  const getData = (columnName, orderBy, search, skip, limit) => {
    setTableFilters(columnName, orderBy, search, skip, limit);
    if (search && skip !== 0) {
      dispatch(
        getAllDishAdminSaga({
          columnName,
          orderBy,
          search,
          skip: 0,
          limit,
          chefId,
        }),
      );
    } else {
      dispatch(
        getAllDishAdminSaga({
          columnName,
          orderBy,
          search,
          skip,
          limit,
          chefId,
        }),
      );
    }
  };

  useEffect(() => {
    setMounted(true);
    getData('name', 'asc', '', 0, 10);
    return () => setMounted(false);
  }, []);

  const tableFields = [
    {
      displayName: 'Image',
      key: 'thumbnail',
      // baseURL: `${process.env.REACT_APP_END_POINT_IMG_URL}/dish/`,
      isMedia: true,
      mediaType: 'image',
      noSort: true,
    },
    {
      displayName: 'Name',
      key: 'name',
    },
    {
      displayName: 'Category',
      key: 'categoryName',
    },
    // {
    //   displayName: 'Sub Category',
    //   key: 'isVeg',
    //   isConditional: true,
    //   positiveValue: 'Veg',
    //   negativeValue: 'Non-Veg',
    // },
    {
      displayName: 'Price (₹)',
      key: 'price',
      positiveValue: true,
    },
    {
      displayName: 'Discount Price (₹)',
      key: 'discountPrice',
      positiveValue: true,
    },
    {
      displayName: 'Description',
      key: 'description',
    },
  ];

  const searchFlagSetter = val => {
    if (mounted) {
      setSearchFlag(val);
    }
  };

  return (
    <div className="wrapper wrapper-content" style={{ padding: 2 }}>
      <div className="box-heading">
        <h2>Menu Items </h2>
      </div>
      <div className="coman-table" id="owner-detail-btn">
        <Table
          headers={tableFields}
          tableData={allData || null}
          pageCount={totalRecords?.count}
          getData={getData}
          linkPropsName="_id"
          setSearchFlag={searchFlagSetter}
          errorMsg={errorMsg || 'Loading...'}
        />
      </div>
    </div>
  );
};

export default SetTokenHeader(MenuItems, axios);
