/* eslint-disable max-len */
/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable react/jsx-no-target-blank */
import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter, Link } from 'react-router-dom';
import moment from 'moment';
import Rate from 'rc-rate';
import CardWithBack from '../../../components/CardWithBack/CardWithBack';
import RejectChefModal from './RejectChefModal';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';
import { axios } from '../../../http';
import Map from '../../../components/UI/Map/Map';
import {
  getChefByIdSaga,
  editBlockChefStatusSaga,
  // chefApproveRejectSaga,
  editAcceptingOrderSaga,
  resetChefDetail,
} from '../../../store/actions';
import { CHEF_NOT_SUBMIT_PROFILE } from '../../../constants/errorConstants';
import MenuItems from './MenuItems';
import ChefCommissionModal from './ChefCommissionModal';

// eslint-disable-next-line no-unused-vars
const ChefDetail = props => {
  // eslint-disable-next-line no-unused-vars
  const [mounted, setMounted] = useState(false);
  const [modelOpen, setModelOpen] = useState(false);
  const [commissionModelOpen, setCommissionModelOpen] = useState(false);
  const {
    chef,
    bankDetail,
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.chef);
  const { open } = useSelector(state => state.modal);
  const address = [];
  // const baseURL = process.env.REACT_APP_END_POINT_IMG_URL;

  const dispatch = useDispatch();

  useEffect(() => {
    const { id } = props.match.params;
    setMounted(true);
    dispatch(getChefByIdSaga({ id }));
    return () => {
      dispatch(resetChefDetail());
      setMounted(false);
    };
  }, []);

  const findRatingLabel = number => {
    if (number <= 5 && number >= 4.51) {
      return 'Excellent';
    }
    if (number <= 4.5 && number >= 4) {
      return 'Good';
    }
    if (number <= 4 && number >= 3.5) {
      return 'Average';
    }
    return 'Opps';
  };

  const genrateAddress = () => {
    if (chef.restaurantAddress) {
      const { restaurantAddress } = chef;
      const {
        apartment,
        area,
        city,
        houseNo,
        landmark,
        pincode,
        street,
      } = restaurantAddress;
      if (houseNo) {
        address.push(houseNo);
      }
      if (apartment) {
        address.push(apartment);
      }
      if (landmark) {
        address.push(landmark);
      }
      if (area) {
        address.push(area);
      }
      if (city) {
        address.push(city);
      }
      if (street) {
        address.push(street);
      }
      if (pincode) {
        address.push(pincode);
      }
    }
  };

  if (chef._id !== undefined) {
    genrateAddress();
  }

  const statusAction = () => {
    const { id } = props.match.params;
    dispatch(
      editBlockChefStatusSaga({
        id,
        isBlock: !chef.isBlock,
        isDetail: true,
      }),
    );
  };

  const AccessSection = () => {
    // const { id } = props.match.params;
    return chef.isApproved !== true ? (
      <div className="col-lg-6 col-md-12 col-sm-12 col-xs-12">
        <div className="clearfix text-right">
          <button
            type="submit"
            className="btn btn-danger m-r-sm"
            onClick={() => {
              setModelOpen(true);
            }}
          >
            Reject
          </button>
          <button
            type="submit"
            className="btn btn-primary"
            style={{ borderRadius: 3 }}
            onClick={() => {
              // dispatch(
              //   chefApproveRejectSaga({
              //     id,
              //     isApproved: true,
              //     isRejected: false,
              //   }),
              // );
              setCommissionModelOpen(true);
            }}
          >
            Accept
          </button>
        </div>
      </div>
    ) : (
      <div className="col-lg-6 col-md-12 col-sm-12 col-xs-12">
        <div className="box-upper text-right ">
          Manage Access:&nbsp;
          <span style={{ marginRight: '10px' }}>
            {!chef.isBlock ? (
              <span className="active">Active</span>
            ) : (
              <span className="deactive">Deactive</span>
            )}
          </span>
          <label className="switch">
            <input
              type="checkbox"
              checked={!chef.isBlock}
              onChange={statusAction}
            />
            <span className="slider round" />
          </label>
        </div>
      </div>
    );
  };

  return (
    <CardWithBack
      pageTitle="Vendor Details"
      rightTopSection={
        (chef.isProfileSubmitted || chef.isApproved) && <AccessSection />
      }
      dataLoadeed={chef._id !== undefined}
      errorMsg={errorMsg}
    >
      {chef.isProfileSubmitted || chef.isApproved || chef.isRejected ? (
        <div id="tab-costum">
          <div className="tabs-container" id="tab-new">
            <ul className="nav nav-tabs" id="tab-chef">
              <li className="active">
                <a data-toggle="tab" href="#tab-3">
                  Vendor Details
                </a>
              </li>
              <li className="">
                <a data-toggle="tab" href="#tab-1">
                  Personal Details
                </a>
              </li>
              <li className="">
                <a data-toggle="tab" href="#tab-2">
                  Banking Details
                </a>
              </li>
              {chef.isApproved ? (
                <li className="">
                  <a data-toggle="tab" href="#tab-4">
                    Payout Details
                  </a>
                </li>
              ) : (
                <li className="">
                  <a data-toggle="tab" href="#tab-5">
                    Menu Items
                  </a>
                </li>
              )}
            </ul>
            <div className="tab-content">
              <div id="tab-1" className="tab-pane">
                <div className="panel-body">
                  <div className="border-solid">
                    <div className="row">
                      <div className="col-md-12">
                        <div className="row ">
                          <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                            <div className="detail-title-a">
                              <p>
                                <span>Name:</span>
                                {`${chef.name}`}
                              </p>
                              <p>
                                <span>Email Id:</span>
                                {chef.email}
                              </p>
                            </div>
                          </div>
                          <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                            <div className="detail-title-a">
                              <p>
                                <span>Contact Number: </span>
                                {chef.phone}
                              </p>
                              <p>
                                <span>Address:</span>
                                {chef.address}
                              </p>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div id="tab-2" className="tab-pane">
                <div className="panel-body">
                  <div className="row">
                    <div className="col-md-12">
                      <div className="row ">
                        <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                          <div className="detail-title-a">
                            <p>
                              <span>Bank Name:</span>
                              {bankDetail.name}
                            </p>
                            <p>
                              <span>Account Holder:</span>
                              {bankDetail.accountHolder}
                            </p>
                            <p>
                              <span>GST Number:</span>
                              {bankDetail.gstNumber}
                            </p>
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                          <div className="detail-title-a">
                            <p>
                              <span>Account No: </span>
                              {bankDetail.accountNumber}
                            </p>
                            <p>
                              <span>IFSC Code:</span>
                              {bankDetail.ifscCode}
                            </p>
                            <p>
                              <span>SWIFT Code:</span>
                              {bankDetail.swiftCode}
                            </p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div id="tab-3" className="tab-pane active">
                <div className="panel-body">
                  <div className="row" id="restorent-detail">
                    <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                      <div className="detail-title-a">
                        <p>
                          <span>Vendor Name:</span>
                          {chef.restaurantName}
                        </p>
                        <p>
                          <span>Vendor Contact Number: </span>
                          {chef.retsaurantContact}
                        </p>
                        {chef.isApproved && (
                          <p>
                            <span>Accepting Orders:</span>
                            <label className="switch">
                              <input
                                type="checkbox"
                                checked={chef.isAcceptingOrders}
                                onChange={() => {
                                  dispatch(
                                    editAcceptingOrderSaga({
                                      status: !chef.isAcceptingOrders,
                                      chefId: chef.userId,
                                    }),
                                  );
                                }}
                              />
                              <span className="slider round" />
                            </label>
                          </p>
                        )}
                      </div>
                    </div>
                    <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                      <div className="detail-title-a">
                        {chef.isApproved ? (
                          <p>
                            <span className="width-150-a">Vendor Rating: </span>
                            <Link
                              to={`/chefs/rating-detail/${props.match.params.id}`}
                            >
                              {/* <i className="fa fa-star" aria-hidden="true" />
                          <i className="fa fa-star" aria-hidden="true" />
                          <i className="fa fa-star" aria-hidden="true" />
                          <i className="fa fa-star-half-o" aria-hidden="true" />
                        <i className="fa fa-star-o" aria-hidden="true" /> */}
                              {/* <i className="fa fa-heart" aria-hidden="true" /> */}
                              <div className="btn btn-white btn-bitbucket btn-rating">
                                <i className="fa fa-heart" />
                                {Number.parseFloat(chef.avgRating).toFixed(1)}
                              </div>
                              {`${findRatingLabel(
                                Number.parseFloat(chef.avgRating).toFixed(1),
                              )} (${chef.totalRatingCount})`}
                            </Link>
                          </p>
                        ) : null}
                        <p>
                          <span>Vendor Description: </span>
                          {chef.retsaurantDescription}
                        </p>
                        <p>
                          <span>Cost for two: </span>
                          <Rate
                            defaultValue={chef.avgPrice}
                            count="5"
                            allowHalf={false}
                            allowClear={false}
                            style={{ fontSize: 15, marginTop: -4 }}
                            character="₹"
                            disabled
                          />
                        </p>
                      </div>
                    </div>
                  </div>
                  <div className="row" id="restorent-detail">
                    <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <div className="detail-title-a">
                        <p>
                          <span>Vendor Address:</span>
                          {chef.restaurantAddress && address.join(', ')}
                        </p>
                      </div>
                    </div>
                  </div>
                  {chef.orderCount !== undefined && chef.isApproved ? (
                    <div className="row">
                      {/* <div className="row" id="restorent-detail"> */}
                      <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                        <div className="detail-title-a">
                          <p>
                            <span>Active Orders:</span>
                            {chef.orderCount.activeOrderCount}
                          </p>
                        </div>
                      </div>
                      {/* </div>
                    <div className="row" id="restorent-detail"> */}
                      <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                        <div className="detail-title-a">
                          <p>
                            <span>Rejected Orders:</span>
                            {chef.orderCount.rejectedOrderCount}
                          </p>
                        </div>
                      </div>
                      {/* </div>
                    <div className="row" id="restorent-detail"> */}
                      <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                        <div className="detail-title-a">
                          <p>
                            <span>Total Orders:</span>
                            {chef.orderCount.totalOrderCount}
                          </p>
                        </div>
                      </div>
                      {/* </div> */}
                    </div>
                  ) : null}
                  <div className="detail-title-a section1  p-b-20">
                    <div className="box-heading">
                      <h2
                        className="p-t-lg m-b-md img-bot"
                        style={{ marginBottom: '10px' }}
                      >
                        Vendor Media
                      </h2>
                    </div>
                    <div className="row" id="kyc-doc">
                      {chef.media &&
                        chef.media.map((item, index) =>
                          index === 0 ? (
                            <div
                              className="col-lg-2 col-md-4 col-sm-3 col-xs-6"
                              key={item.name}
                            >
                              <a target="_blank" href={`${item.name}`}>
                                <div className="box">
                                  {item.type === 'image' ? (
                                    <img
                                      src={`${item.thumbnail}`}
                                      alt=""
                                      className="myImg"
                                    />
                                  ) : (
                                    <img
                                      src={`${item.thumbnail}`}
                                      alt=""
                                      className="myImg"
                                    />
                                  )}
                                </div>
                              </a>
                            </div>
                          ) : (
                            <div
                              className="col-lg-2 col-md-4 col-sm-3 col-xs-6"
                              key={item.name}
                            >
                              <a target="_blank" href={`${item.name}`}>
                                <div className="box">
                                  {item.type === 'image' ? (
                                    <img
                                      src={`${item.thumbnail}`}
                                      alt=""
                                      className="myImg"
                                    />
                                  ) : (
                                    <img
                                      src={`${item.thumbnail}`}
                                      alt=""
                                      className="myImg"
                                    />
                                  )}
                                </div>
                              </a>
                            </div>
                          ),
                        )}
                      {chef.media && chef.media.length === 0 && (
                        <div className="noDocFiles">No Media Found</div>
                      )}
                    </div>
                  </div>
                  <div className="detail-title-a section1  p-b-20">
                    <div className="box-heading">
                      <h2
                        className="p-t-lg m-b-md img-bot"
                        style={{ marginBottom: '10px' }}
                      >
                        Legal Documents
                      </h2>
                    </div>
                    <div className="row" id="kyc-doc">
                      {chef.idProof &&
                        chef.idProof.map(item => (
                          <div
                            className="col-lg-2 col-md-4 col-sm-3 col-xs-6"
                            key={item.name}
                          >
                            <a target="_blank" href={`${item.name}`}>
                              <div className="box">
                                {item.type === 'image' ? (
                                  <img
                                    src={`${item.thumbnail}`}
                                    alt=""
                                    className="myImg"
                                  />
                                ) : (
                                  <img
                                    src={
                                      item.name.includes('pdf')
                                        ? require('../../../assets/img/pdf.jpg')
                                        : require('../../../assets/img/doc.jpg')
                                    }
                                    alt=""
                                    className="myImg idproof-doc"
                                  />
                                )}
                              </div>
                            </a>
                          </div>
                        ))}
                      {chef.idProof && chef.idProof.length === 0 && (
                        <div className="noDocFiles">
                          No Legal Documents Found
                        </div>
                      )}
                    </div>
                  </div>
                  <div className="row" id="tab-costuma">
                    <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                      <div className="detail-title-a">
                        <p>
                          <span>
                            Payment Options <br />
                            (COD/Online):
                          </span>
                          {chef.paymentOptions}
                        </p>
                      </div>
                    </div>
                    <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                      <div className="detail-title-a">
                        <p>
                          <span>
                            Accept Order Type <br />
                            (Self pickup / Delivery):
                          </span>
                          {chef.orderType}
                        </p>
                      </div>
                    </div>
                  </div>
                  <div
                    className="row"
                    id="tab-costuma"
                    style={{ marginBottom: 5 }}
                  >
                    <h2
                      className="detail-title-a"
                      style={{ marginLeft: '10px' }}
                    >
                      Delivery type &amp; Schedule
                    </h2>
                    <div className="col-md-12">
                      <div className="detail-title-a">
                        <p>
                          <span>Instant Delivery:</span>
                          {chef.instantDelivery ? 'Yes' : 'No'}
                        </p>
                      </div>
                    </div>
                    {!chef.instantDelivery && (
                      <>
                        <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                          <div className="detail-title-a">
                            <p>
                              <span>Opening Days:</span>

                              {chef.openDays &&
                                `${chef.openDays.split(', ').length} days (${
                                  chef.openDays.split(', ').length === 7
                                    ? 'All'
                                    : chef.openDays
                                })`}
                            </p>
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                          <div className="detail-title-a">
                            <p>
                              <span>Opening Hours:</span>
                              {`${moment(chef.startTime, 'HH:mm').format(
                                'hh:mm A',
                              )} to ${moment(chef.endTime, 'HH:mm').format(
                                'hh:mm A',
                              )}`}
                            </p>
                          </div>
                        </div>
                      </>
                    )}
                  </div>
                  <Map lat={chef.lat} lng={chef.long} />
                </div>
              </div>
              <div id="tab-4" className="tab-pane">
                <div className="panel-body" id="color">
                  <div className="row m-b-md" id="tab-costuma">
                    <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                      <div className="detail-title-a">
                        <p>
                          <span>Total Payout:</span>
                          {chef.payout ? chef.payout.totalPaid : '0'}
                        </p>
                        <p>
                          <span>Last Payout:</span>
                          {chef.payout ? chef.payout.lastPayout : '0'}
                        </p>
                      </div>
                    </div>
                    <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                      <div className="detail-title-a">
                        <p>
                          <span>Total Pending Payout:</span>
                          {chef.payout ? chef.payout.totalPending : '0'}
                        </p>
                        <p>
                          <span> Last Payout Date:</span>
                          {chef.payout
                            ? moment(chef.payout.lastPayoutDate).format(
                                'L hh:mm A',
                              )
                            : ''}
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div id="tab-5" className="tab-pane">
                <div className="panel-body" id="color">
                  <MenuItems chefId={props.match.params.id} />
                </div>
              </div>
            </div>
          </div>
          {modelOpen && (
            <RejectChefModal
              setModelOpen={setModelOpen}
              chefId={props.match.params.id}
            />
          )}
          {commissionModelOpen && (
            <ChefCommissionModal
              setModelOpen={setCommissionModelOpen}
              chefId={props.match.params.id}
            />
          )}
          {open && <AlertMessageModal />}
        </div>
      ) : (
        <div className="error-card-div">{CHEF_NOT_SUBMIT_PROFILE}</div>
      )}
    </CardWithBack>
  );
};

export default SetTokenHeader(withRouter(ChefDetail), axios);
